.sidebar {
  position: fixed;
  top: 0;
  left: 0;

  transform: translateX(-130%);

  display: none;

  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 1440px) {
    display: block;

    transform: translateX(0);
  }
}

.active {
  display: block;
  z-index: 1;

  transform: translateX(0);
}

.context {
  position: fixed;
  top: 0;
  right: 0;

  width: 100%;
  height: 100vh;

  @media screen and (min-width: 1440px) {
    width: calc(100% - 289px);
  }
}
